<template>
    <view
    >
        <view class="hot-post"
        v-if="bodyData.content.length > 0"
        >
            <view class="hot-post-title">
                <image :src="$kt.file.byPath('image/icon/hot.svg')" mode="widthFix" class="hot-post-title-icon"></image>
                <text>热帖</text>
            </view>

            <!-- 轮播图 -->
            <swiper class="hot-post-swiper" :indicator-dots="false" autoplay="true" interval="3000" duration="500">
                <swiper-item v-for="(item, index) in bodyData.content" :key="index">
                    <view class="box">
                        <image v-if="item.imageList.length > 0" :src="$kt.file.visit(item.imageList[0].fileId)"
                            mode="aspectFill" class="box-image" />
                        <view class="box-text">{{ item.content }}</view>
                    </view>
                </swiper-item>
            </swiper>

        </view>
    </view>
</template>

<script>
export default {
    data() {
        return {
            bodyData: {
                content: []
            }
        }
    },
    mounted() {
        this.getBodyData();
    },
    methods: {
        async getBodyData() {
            this.$kt.request.request({
                api: this.$api.business.ovo.post.getHot,
                stateSuccess: (res) => {
                    this.bodyData.content = res.data;
                }
            });
        }
    },
}
</script>


<style lang="scss" scoped>
.hot-post {
    background-color: #fff;
    overflow: hidden;
    padding: 0 20rpx 20rpx 20rpx;
    box-sizing: border-box;

    .hot-post-title-icon {
        width: 50rpx;
        height: 50rpx;
        margin-right: 10rpx;
        vertical-align: middle;
        vertical-align: bottom;
    }

    .hot-post-title {
        padding: 10rpx;
        font-size: 36rpx;
        font-weight: bold;
        vertical-align: bottom;
    }

}

.hot-post-swiper {
    width: 100%;
    height: 400rpx;
    margin-top: 20rpx;
    border-radius: 10rpx;
    overflow: hidden;

    .swiper-item {
        width: 100%;
        height: 100%;
    }
}

.box {
    position: relative;
    width: 100%;
    height: 400rpx;
    background-color: #fff;
    border-radius: 10rpx;

    .box-image {
        width: 100%;
        height: 100%;
        border-radius: 10rpx;
    }

    .box-text {
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        background-color: rgba(0, 0, 0, .8);
        padding: 10rpx;
        color: #fff;
        font-size: 28rpx;
        border-radius: 0 0 10rpx 10rpx;
    }
}
</style>